html {
  font-size: 100%;
  box-sizing: border-box;
}

body {
  border: none;
  outline: none;
  overflow-x: hidden;
}

* {
  margin: 0;
  padding: 0;
}

*::selection {
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-drag: none;
}

*::-webkit-scrollbar,
*::-webkit-scrollbar-track,
*::-webkit-scrollbar-thumb {
  display: none;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

a,
a:active,
a:visited {
  color: var(--white);
  text-decoration: none !important;
}

h1,
h2,
h3,
h4,
p {
  margin: 0;
  padding: 0;
  line-height: 1;
}

/* 640px - 1024px - 1400px */
/* 639px - 1023px - 1399px */
.crsr {
  position: fixed;
  top: -3.125em;
  left: -3.125em;
  transform: translate(-49.55%, -50.25%);
  mix-blend-mode: difference;
  pointer-events: none;
  z-index: 9999;
}
.crsr::before {
  content: "";
  width: 5.625em;
  height: 5.625em;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  transform: scale(0.15);
  background: whitesmoke;
  box-shadow: 0 0 1.25em cyan, 0 0 3.75em cyan, 0 0 6.25em cyan;
  border: 0.0625em solid cyan;
  animation: colors 7s infinite;
  transition: all 200ms;
  transition-property: content transform opacity border background;
  transition-timing-function: cubic-bezier(0.6, -0.28, 0.735, 0.045);
}

.crsr.brnd {
  mix-blend-mode: unset;
}
.crsr.brnd::before {
  content: "";
  transform: scale(0.85);
  opacity: 0.25;
  border: none;
  background: linear-gradient(45deg, cyan, dodgerblue);
}

.crsr.hmbrgr {
  mix-blend-mode: unset;
}
.crsr.hmbrgr::before {
  content: "";
  transform: scale(0.85);
  opacity: 0.15;
  border: none;
  background: linear-gradient(45deg, grey, red);
}

.crsr.nvlnks {
  mix-blend-mode: unset;
}
.crsr.nvlnks::before {
  content: "";
  transform: scale(0.85);
  opacity: 0.15;
  border: none;
  background: linear-gradient(45deg, orange, magenta);
}

.crsr.bnnrdstntnlnks {
  mix-blend-mode: unset;
}
.crsr.bnnrdstntnlnks::before {
  content: "";
  transform: scale(0.75);
  opacity: 0.25;
  border: none;
  background: linear-gradient(45deg, black, blue);
}

.crsr.bnnrcrwlnks {
  mix-blend-mode: unset;
}
.crsr.bnnrcrwlnks::before {
  content: "";
  transform: scale(0.55);
  opacity: 0.25;
  border: none;
  background: linear-gradient(120deg, orange, indigo);
}

.hmbrgr {
  display: none;
}
@media screen and (max-width: 39.9375em) {
  .hmbrgr {
    display: initial;
    margin-right: 2.234375em;
  }
}
.hmbrgr__mn {
  background-color: transparent;
  cursor: grab !important;
  border: none;
  display: flex;
  padding: 0;
  outline: none;
  z-index: 1000;
}
.hmbrgr__line {
  fill: none;
  stroke: whitesmoke;
  stroke-width: 3;
  transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1), stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1);
}
.hmbrgr__line1 {
  stroke-dasharray: 60 207;
  stroke-width: 3;
}
.hmbrgr__line2 {
  stroke-dasharray: 60 60;
  stroke-width: 3;
}
.hmbrgr__line3 {
  stroke-dasharray: 60 207;
  stroke-width: 3;
}
.shw .hmbrgr__line1 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 3;
}
.shw .hmbrgr__line2 {
  stroke-dasharray: 1 60;
  stroke-dashoffset: -30;
  stroke-width: 3;
}
.shw .hmbrgr__line3 {
  stroke-dasharray: 90 207;
  stroke-dashoffset: -134;
  stroke-width: 3;
}

.prldr {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  background: #252755;
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 9;
  transition-duration: 1s;
  transition-timing-function: cubic-bezier(0.86, 0, 0.09, 1);
  transition-delay: 1s;
  transition-delay: 1s;
}
.prldr .ldbr {
  width: 0%;
  height: 100%;
  max-height: 0.375em;
  background: linear-gradient(45deg, #09e7e7, #252755);
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  overflow: hidden;
}
.prldr figure {
  width: 37.5em;
  height: 37.5em;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: fade 300ms;
  transition-duration: 1s;
  transition-timing-function: cubic-bezier(0.86, 0, 0.09, 1);
  position: relative;
}
.prldr figure span {
  width: 100%;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  color: lightcyan;
  text-align: center;
  letter-spacing: 0.1875em;
  font-family: fantasy;
  font-size: 1em;
}
.prldr figure img {
  height: 6.25em;
  display: inline-block;
  opacity: 0.05;
}

@keyframes fade {
  0% {
    transform: translateY(-50px);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
.pgldd .prldr {
  height: 0;
}
.pgldd .prldr .ldbr {
  max-height: 100%;
  transition-duration: 1s;
  transition-timing-function: cubic-bezier(0.86, 0, 0.09, 1);
}
.pgldd .prldr figure {
  transform: translateY(50px);
  opacity: 0;
}

